}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    align-self: flex-end;
}

.text {
    text-align: end;
    position: relative;
    max-width: 80%;
    width: fit-content;
    margin-right: 16px;
  
    padding: 7px 13px;

    color: white;
    font-family: inherit;

    background-color: #0066CC;
    border-radius: 6px 6px 0 6px;

}

.text::after {
    content: '';
    position: absolute;
    border: 6px solid transparent;
    border-bottom: 6px solid #0066CC;
    border-left: 6px solid #0066CC;
    right: -12px;
    bottom: 0;
}


.messageText {
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    word-wrap: break-word;
}

.name {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 4px;
}

.messageText {
    font-size: 16px;
    font-weight: 400;
}

.time {
    position: absolute;
}